<template>    <div class="filter">        <button class="btn btn-slim filter-btn" @click.prevent="toggleFilters">Фильтр</button>        <ul :class="{show:showFilters}" class="filter__options">            <li :class="{active:isActive(filter.idTag)}" v-for="(filter,index) in filters">                <a href="#" @click.prevent="addFilter(filter.idTag)">{{filter.tagName}}</a>            </li>        </ul>    </div></template><script>    module.exports = {        methods: {            isActive: function (idTag) {                if (store.state.filters.indexOf(idTag) >= 0) {                    return true;                } else {                    return false;                }            },            addFilter: function (idTag) {                store.commit('addFilter', {'value': idTag});            },            toggleFilters: function () {                if (this.showFilters) {                    this.showFilters = 0;                } else {                    this.showFilters = 1;                }            }        },        created() {        },        data() {            return {                showFilters: 0            }        },        ready: function () {        },        props: ['filters'],        mounted: function () {            var reference = document.querySelector('.filter');            var popper = document.querySelector('.filter__options');            var anotherPopper = new Popper(                reference,                popper,                {                    // popper options here                }            );        },        computed: {}    }</script><style scoped lang="scss">    .filter ul li.active {        background:#000;    }    .filter ul li.active a {        color:#fff;    }</style>